
import tools from '@/common/tools.js'
import axios from 'axios'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
name: 'refunddetails',
data: function() {
return {
recId: '',
allData: {},
confirmShow: false,
tuikuanId: '',
lastTime: '',
endTime: ''
}
},
activated() {
this.recId = this.$route.query.recId
console.log(this.recId)
this._getRefundDetailsData()
},
directives: {
TransferDom
},
methods: {
countdown(time) {

const now = Date.parse(new Date())

const msec = this.endTime - now

let day = parseInt(msec / 1000 / 60 / 60 / 24)
let hr = parseInt(msec / 1000 / 60 / 60 % 24)
let min = parseInt(msec / 1000 / 60 % 60)
let sec = parseInt(msec / 1000 % 60)

hr = hr > 9 ? hr : '0' + hr
min = min > 9 ? min : '0' + min
sec = sec > 9 ? sec : '0' + sec

day = day > 0 ? day + '天' : ''
hr = hr > 0 ? hr + '小时' : ''
min = min > 0 ? min + '分钟' : ''
sec = sec > 0 ? sec + '秒' : ''

this.lastTime = day + hr + min + sec
const $this = this
if (day <= 0 && hr <= 0 && min <= 0 && sec <= 0) {
window.location.reload()
return false
}
setTimeout(function () {
$this.countdown()
}, 1000)
},
uploadCert(id) {
this.$router.push({path:'uploadcert', query: {returnId: id}})
},
onConfirm() {
console.log('我要开始删除了')

let url = '/Mobile/Order/return_goods_cancel.html'
axios.get(url, {
params: {
id: this.tuikuanId
}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
this.confirmShow = false

tools.showError(res.data.msg)
this._getRefundDetailsData()
} else {
tools.showError(res.data.msg)
}
})
.catch((error) => {
console.log(error)
})
},
onCancel() {
this.confirmShow = false
},
delReturnGood(id) {
console.log(id)
this.confirmShow = true
this.tuikuanId = id
},
_getRefundDetailsData() {
let url = '/Mobile/Order/return_goods_info.html'
axios.get(url, {
params: {
id: this.recId
}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
this.allData = res.data.data
if (!this.allData.remaining_time) {
this.endTime = Date.parse(new Date(this.allData.remaining_time * 1000))

this.countdown()
}
} else {
tools.showError(res.data.msg)
setTimeout(() => {
this.$router.go(-1)
}, 2000)
}
})
.catch((error) => {
console.log(error)
})
}
},
components: {
Confirm
}
}



